import React from "react";
import {connect} from "react-redux";
import {Sticker} from "../../../configurator/components/lp/offers/Sticker";

export default  class ProductGalleryLargePictureComponent extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.loadModules();
    }
    componentWillUpdate(){
        ProductGalleryLargePictureComponent.killModules();
    }
    static killModules(){
        OPL.UI.stopModules(document.getElementById("main-picture-container"));
    }
    componentDidUpdate(){
        this.loadModules();
    }

    loadModules() {
        OPL.UI.loadModules(this.ref, [{
                path: 'common/modules/opl-width-fixer',
                options: { item: ".opl-wiking-gallery-single--item", extendHeight: 20 }
            },
            {
                path: 'common/modules/opl-carousel',
                options: {
                    asNavFor: ".opl-sticked-element .opl-wiking-gallery-navigation",
                    slidesToScroll: 1,
                    slidesToShow: 1,
                    arrows: false,
                    slide: ".opl-wiking-gallery-single--item",
                    dots: false,
                    fade: true,

                    responsive: [{
                        breakpoint: 767,
                        settings: {
                            dots: true
                        }
                    }]
                }
            }, {
                path: "common/modules/opl-image-zoom",
                conditions: "media:{gt-medium}",
                options: {
                    zoomWindowOffsetY: 70,
                    zoomContainerAppendTo: ".opl-product-page",
                    zoomWindowOffsetXAlignContainer: "#product-basic-information",
                    zoomWindowWidthAlignContainer: "#product-basic-information",
                }
            }

        ]);
    }
    render() {
         return (
            <div  ref={ref=> this.ref = ref}
                className="opl-wiking-gallery-single u-margin" >
                 {this.props.productVariant.mediaContent.map(
                    (element,index) => <CarouselItem url={element.mediaImageUrl} alt={element.altText} key={index} sticker={this.props.sticker} selectedOfferType={this.props.selectedOfferType} />
                    )
                 }
            </div>
    )
    }

}

const CarouselItem = (props) =>{

    return (
        <div data-image-large={props.url}  className="opl-wiking-gallery-single--item u-position-relative" >
            <div className="opl-wiking-gallery-single--wrapper u-position-relative">
               <Sticker sticker = {props.sticker} className={"u-position-absolute"}/>
               <img src={props.url} alt={props.alt} data-zoom-image={props.url} className="image-zoom-item"  />
            </div>
        </div>
    )

};